import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { createPeer, generateAnswer, setRemoteDescription } from '../controllers/WebRTC'; // Adjust the import path
import '../../App.css';

function ConnectPhone() {
    const navigate = useNavigate();
    const [width, setWidth] = useState(window.innerWidth);
    const [peer, setPeer] = useState(null);
    const [offer, setOffer] = useState('');
    const [answer, setAnswer] = useState('');
    const [input, setInput] = useState('');
    const [messages, setMessages] = useState([]);
    const isMobile = width <= 768;

    useEffect(() => {
        function handleWindowSizeChange() {
            setWidth(window.innerWidth);
        }

        window.addEventListener('resize', handleWindowSizeChange);

        return () => {
            window.removeEventListener('resize', handleWindowSizeChange);
        };
    }, []);

    useEffect(() => {
        if (isMobile) {
            // navigate(`/mobile/`);
        } else {
            // Initialize peer and setup the desktop environment
            const newPeer = createPeer(true, 
                (data) => setOffer(JSON.stringify(data)),
                (data) => setMessages(prevMessages => [...prevMessages, { text: data.toString(), sender: 'Remote' }]),
                () => alert("Connected!"),
                () => alert("The connection has been closed.")
            );
            setPeer(newPeer);
        }
    }, [isMobile, navigate]);

    const handleGenerateAnswer = () => {
        if (peer) {
            generateAnswer(peer, offer, (data) => setAnswer(JSON.stringify(data)));
        }
    };

    const handleSetRemoteDescription = () => {
        setRemoteDescription(peer, answer);
    };

    const copyToClipboard = (text) => {
        navigator.clipboard.writeText(text)
            .then(() => alert('Copied to clipboard'))
            .catch(err => console.error('Failed to copy text:', err));
    };

    return (
        <div className="App">
            {isMobile ? (
                <div className="mobile">
                    <h1>Mobile App</h1>
                    <div>
                        <label>Offer JSON:</label>
                        <input
                            type="text"
                            value={offer}
                            onChange={e => setOffer(e.target.value)}
                            placeholder="Paste offer JSON here"
                        />
                        <button onClick={handleGenerateAnswer}>Generate Answer</button>
                    </div>
                    <div>
                        <label>Answer JSON:</label>
                        <textarea
                            readOnly
                            value={answer}
                            placeholder="Answer JSON will appear here"
                            rows={4}
                            cols={50}
                        />
                        <button onClick={() => copyToClipboard(answer)}>Copy to Clipboard</button>
                    </div>
                </div>
            ) : (
                <div className="desktop">
                    <h1>Desktop App</h1>
                    <img src={process.env.PUBLIC_URL + '/images/qr-code.png'} width="250px"></img>
                    <div>
                        <label>Offer JSON:</label>
                        <textarea
                            readOnly
                            value={offer}
                            placeholder="Offer JSON will appear here"
                            rows={4}
                            cols={50}
                        />
                    </div>
                    <div>
                        <label>Answer JSON:</label>
                        <input
                            type="text"
                            value={answer}
                            onChange={e => setAnswer(e.target.value)}
                            placeholder="Paste answer JSON here"
                        />
                        <button onClick={handleSetRemoteDescription}>Set Remote Description</button>
                    </div>
                    <div className="chat">
                        {messages.map((msg, index) => (
                            <div key={index} className={msg.sender === 'You' ? 'message you' : 'message remote'}>
                                {msg.text}
                            </div>
                        ))}
                        <input
                            type="text"
                            value={input}
                            onChange={e => setInput(e.target.value)}
                            placeholder="Type a message"
                            onKeyPress={e => e.key === 'Enter' && sendMessage()}
                        />
                        <button onClick={() => {
                            if (peer && input.trim()) {
                                peer.send(input);
                                setMessages(prevMessages => [...prevMessages, { text: input, sender: 'You' }]);
                                setInput('');
                            }
                        }}>Send</button>
                    </div>
                </div>
            )}
        </div>
    );
}

export default ConnectPhone;
