import SimplePeer from 'simple-peer';

// Create a peer and return it
export const createPeer = (isInitiator, onSignal, onData, onConnect, onClose) => {
    const peer = new SimplePeer({ initiator: isInitiator, trickle: false });

    peer.on('signal', data => {
        if (onSignal) onSignal(data);
    });

    peer.on('data', data => {
        if (onData) onData(data);
    });

    peer.on('connect', () => {
        if (onConnect) onConnect();
    });

    peer.on('close', () => {
        if (onClose) onClose();
    });

    return peer;
};

// Set remote description
export const setRemoteDescription = (peer, answer) => {
    if (peer && answer) {
        peer.signal(JSON.parse(answer));
    }
};

// Generate answer and return it
export const generateAnswer = (peer, offer, onSignal) => {
    const newPeer = createPeer(false, onSignal, null, null, null);
    newPeer.signal(JSON.parse(offer));
    return newPeer;
};
